const main = () => {
  return import('./pages/main.vue');
};
const layout = () => {
  return import('./pages/layout.vue');
};
const index = () => {
  return import('./pages/index.vue');
};

const chart = () => {
  return import('./pages/chart.vue');
};

const node = () => {
  return import('./pages/node.vue');
};
const list = () => {
  return import('./pages/list.vue');
};
const listImg = () => {
  return import('./pages/list-server.vue');
};
const content = () => {
  return import('./pages/content.vue');
};

const notFound = () => {
  return import('./pages/404.vue');
};

// 独页
const reg = () => {
  return import('./pages/reg.vue');
};

const login = () => {
  return import('./pages/login.vue');
};

const usercenter = () => {
  return import('./pages/usercenter/main.vue');
};
const usercenter_index = () => {
  return import('./pages/usercenter/index.vue');
};
const usercenter_socket = () => {
  return import('./pages/usercenter/socket/index.vue');
};
const usercenter_socketEdit = () => {
  return import('./pages/usercenter/socket/edit.vue');
};
const usercenter_taskGroup = () => {
  return import('./pages/usercenter/task/taskGroup.vue');
};
const usercenter_taskGroupEdit = () => {
  return import('./pages/usercenter/task/taskGroupEdit.vue');
};

const usercenter_task = () => {
  return import('./pages/usercenter/task/task.vue');
};
const usercenter_taskEdit = () => {
  return import('./pages/usercenter/task/taskEdit.vue');
};

const usercenter_smsSigname = () => {
  return import('./pages/usercenter/sms/signame.vue');
};
const usercenter_smsSignameEdit = () => {
  return import('./pages/usercenter/sms/signameEdit.vue');
};



const routes = [{
  path: '/',
  component: main,
  meta: {
    title: 'asdasd'
  },
  children: [{ // 空路由，什么都没匹配到的时候执行
    path: '',
    component: index
  }, {
    path: 'chart',
    component: chart,
    meta: {
      title: "在线聊天"
    }
  }, {
    path: ':path-:nodeid(\\d+)-:mp(\\d+)list', // 普通列表页
    component: list
  }, {
    path: '/:path-:nodeid(\\d+)-:mp(\\d+)list/:cp(\\d+)', // 普通列表页
    component: list
  }, {
    path: ':path-:nodeid(\\d+)node', // 节点型列表页
    component: node
  }, {
    path: '/:path-:nodeid(\\d+)-:mp(\\d+)img', // 图片列表页
    component: listImg
  }, {
    path: '/:path-:nodeid(\\d+)-:mp(\\d+)img/:cp(\\d+)', // 图片列表页
    component: listImg
  }, {
    path: '/content/:id(\\d+)', // 正文页
    component: content
  }, {
    path: '/content_photo/:id(\\d+)', // 正文页
    component: content
  }, {
    path: '/reg',
    component: reg
  }, {
    path: '/login',
    component: login
  }]
}, {
  path: '/usercenter',
  component: usercenter,
  children: [{
    path: '',
    component: usercenter_index,
  }, {
    path: 'socket',
    component: layout,
    children: [{
      path: '',
      component: usercenter_socket,
      title: "asddas"
    }, {
      path: "edit",
      component: usercenter_socketEdit
    }]
  }, {
    path: 'taskgroup',
    component: layout,
    children: [{
      path: '',
      component: usercenter_taskGroup
    }, {
      path: 'edit',
      component: usercenter_taskGroupEdit
    }, {
      path: 'task/:groupid(\\d+)',
      component: usercenter_task
    }, {
      path: 'task/:groupid(\\d+)/edit',
      component: usercenter_taskEdit
    }]
  }, {
    path: 'sms',
    component: layout,
    children: [{
      path: 'signame',
      component: layout,
      children: [{
        path: '',
        component: usercenter_smsSigname
      }, {
        path: 'edit',
        component: usercenter_smsSignameEdit
      }]
    }]
  }]
}, {
  path: '*',
  component: notFound
}];

module.exports = routes;